'use client';

import React, { useRef, useState } from 'react';
import { motion, useMotionTemplate, useMotionValue, useSpring } from 'framer-motion';

const TiltCard = ({
    children,
    className = '',
    tiltAmount = 10, // degrees of tilt
    glowColor = 'rgba(59, 130, 246, 0.15)',
    scale = 1.02,
}) => {
    const cardRef = useRef(null);
    const [isHovered, setIsHovered] = useState(false);

    // Motion values for tilt
    const rotateX = useMotionValue(0);
    const rotateY = useMotionValue(0);
    
    // Motion values for glow position
    const glowX = useMotionValue(50);
    const glowY = useMotionValue(50);

    // Spring config for smooth movement
    const springConfig = { damping: 20, stiffness: 300 };
    const smoothRotateX = useSpring(rotateX, springConfig);
    const smoothRotateY = useSpring(rotateY, springConfig);
    const smoothGlowX = useSpring(glowX, springConfig);
    const smoothGlowY = useSpring(glowY, springConfig);

    // Create the glow gradient template
    const glowBackground = useMotionTemplate`radial-gradient(350px circle at ${smoothGlowX}% ${smoothGlowY}%, ${glowColor}, transparent 70%)`;

    const handleMouseMove = (e) => {
        if (!cardRef.current) return;
        
        const rect = cardRef.current.getBoundingClientRect();
        const width = rect.width;
        const height = rect.height;
        
        // Calculate mouse position relative to card center (0-1 range, centered at 0.5)
        const x = (e.clientX - rect.left) / width;
        const y = (e.clientY - rect.top) / height;
        
        // Calculate rotation (centered at 0.5, so we subtract 0.5 to get -0.5 to 0.5 range)
        const tiltX = (y - 0.5) * -tiltAmount; // Invert Y for natural feel
        const tiltY = (x - 0.5) * tiltAmount;
        
        rotateX.set(tiltX);
        rotateY.set(tiltY);
        
        // Set glow position (0-100%)
        glowX.set(x * 100);
        glowY.set(y * 100);
    };

    const handleMouseEnter = () => {
        setIsHovered(true);
    };

    const handleMouseLeave = () => {
        setIsHovered(false);
        // Reset to center
        rotateX.set(0);
        rotateY.set(0);
        glowX.set(50);
        glowY.set(50);
    };

    return (
        <motion.div
            ref={cardRef}
            className={`relative ${className}`}
            onMouseMove={handleMouseMove}
            onMouseEnter={handleMouseEnter}
            onMouseLeave={handleMouseLeave}
            style={{
                transformStyle: 'preserve-3d',
                perspective: '1000px',
            }}
            whileHover={{ scale }}
            transition={{ duration: 0.2 }}
        >
            <motion.div
                className="h-full"
                style={{
                    rotateX: smoothRotateX,
                    rotateY: smoothRotateY,
                    transformStyle: 'preserve-3d',
                }}
            >
                {/* Glow overlay */}
                <motion.div
                    className="pointer-events-none absolute inset-0 rounded-xl z-10"
                    style={{
                        background: glowBackground,
                        opacity: isHovered ? 1 : 0,
                        transition: 'opacity 0.3s ease',
                    }}
                />
                
                {/* Shine effect on top edge */}
                <motion.div
                    className="pointer-events-none absolute inset-x-0 top-0 h-px z-10"
                    style={{
                        background: `linear-gradient(90deg, transparent, rgba(255,255,255,0.1) ${smoothGlowX}%, transparent)`,
                        opacity: isHovered ? 1 : 0,
                        transition: 'opacity 0.3s ease',
                    }}
                />
                
                {children}
            </motion.div>
        </motion.div>
    );
};

export default TiltCard;
